.container {
  min-height: 100vh;
  background: linear-gradient(to bottom right, #1a1a1a, #000000);
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem;
}

.card {
  background: rgba(0, 0, 0, 0.4);
  backdrop-filter: blur(8px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 1rem;
  padding: 2rem;
  max-width: 32rem;
  width: 100%;
  position: relative;
  overflow: hidden;
}

.alertPulse {
  position: absolute;
  inset: 0;
  background: rgba(239, 68, 68, 0.2);
  animation: pulse 2s infinite;
}

.scanLine {
  position: absolute;
  inset: 0;
  overflow: hidden;
}

.scanLine::before {
  content: '';
  position: absolute;
  height: 2px;
  width: 100%;
  background: rgba(59, 130, 246, 0.4);
  animation: scan 2s linear infinite;
}

.header {
  text-align: center;
  margin-bottom: 2rem;
}

.icon {
  font-size: 4rem;
  margin-bottom: 1rem;
}

.title {
  font-size: 1.875rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.subtitle {
  color: rgba(255, 255, 255, 0.7);
  font-size: 1.125rem;
}

.message {
  text-align: center;
  margin-bottom: 2rem;
  font-size: 1.125rem;
  line-height: 1.5;
}

.button {
  width: 100%;
  padding: 0.75rem 1rem;
  border-radius: 0.5rem;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  transition: background-color 0.2s;
  background-color: #2563eb;
  cursor: pointer;
}

.button:hover {
  background-color: #1d4ed8;
}

.button:disabled {
  background-color: rgba(37, 99, 235, 0.5);
  cursor: not-allowed;
}

.loading {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

.spinner {
  animation: spin 1s linear infinite;
  width: 1rem;
  height: 1rem;
  border: 2px solid currentColor;
  border-radius: 50%;
  border-right-color: transparent;
}

.error {
  background: rgba(239, 68, 68, 0.1);
  border: 1px solid rgba(239, 68, 68, 0.5);
  border-radius: 0.5rem;
  padding: 1rem;
  color: #fca5a5;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-top: 1rem;
}

.success {
  text-align: center;
  margin-top: 2rem;
}

.successIcon {
  font-size: 4rem;
  margin-bottom: 1rem;
  color: #34d399;
}

.successText {
  color: #34d399;
  margin-bottom: 0.5rem;
}

.redirectText {
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.875rem;
}

.footer {
  margin-top: 2rem;
  padding-top: 1rem;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.875rem;
}

@keyframes pulse {
  0% { opacity: 0.2; }
  50% { opacity: 0.4; }
  100% { opacity: 0.2; }
}

@keyframes scan {
  from { transform: translateY(-100%); }
  to { transform: translateY(100%); }
}

@keyframes spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}
